/*** CSS FOR MOBILE ***/

html {
  font-family: "Open Sans", sans-serif;
  background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/***PROJECT PART***/
.project-container {
  color: white;
  display: flex;
  overflow: auto;
  justify-content: start;
  font-size: calc(5px + 1vw);
  gap: 2%;
  padding: calc(10px + 1.5vw);

  border-color: #2c0000; /* Border shows white!!! */
  border-top: 5px solid;
  border-bottom: 5px solid;
  background: rgb(38, 38, 38);
  background: linear-gradient(
    90deg,
    rgba(38, 38, 38, 1) 0%,
    rgba(31, 0, 0, 1) 52%,
    rgba(34, 34, 34, 1) 100%
  );
  width: 100%;
}

.archives {
  color: white;
  display: block;
  align-items: center;
  border-bottom: 10px;
}

.newest-project {
  font-size: calc(3px + 2vw);
  overflow: auto;
  background-color: rgb(44, 0, 0);
  color: white;
  display: flex;
  align-items: center;
  gap: 3%;
  padding: calc(30px + 1w);
  margin: 10px;
}

/*** MORE INFO BUTTON ***/
.more-info-button {
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.more-info-button a {
  color: white;
  text-decoration: none;
}

/*** STUFF IDK ***/
.centered-thing {
  text-align: center;
  color: white;
  margin-top: 10%;
}

.centered {
  text-align: center;
}

.big-text {
  font-size: calc(10px + 1.5vw);
  margin: 2%;
}

/***WELCOME PAGE***/
.welcome-title {
  color: white;
  /*display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enter-button {
  background-color: rgba(255, 0, 0, 0.411);
  text-decoration: none;
  color: white;
  padding: 0.3rem;
  position: fixed;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/*** NAVBAR ***/

.make-navbar-horizontal {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(25, 0, 4, 1) 48%,
    rgba(65, 0, 0, 1) 100%
  );
}

.site-title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 30px;
  color: white;
}

.site-title a {
  text-decoration: none;
  color: inherit;
}

nav {
  color: white;
  gap: 5%;
  overflow: auto;
  width: 100%;
}

nav ul {
  padding-left: calc(50px + 1.5vw);
  margin: 20px;
  list-style: none;
  display: flex;
  gap: 1%;
  margin-left: 0px;
}

nav a {
  font-size: calc(15px + 2vw);
  color: inherit;
  text-decoration: none;
  border-left: 3px solid;
  border-right: 3px solid;
  border-color: red;
}

nav li.active {
  background-color: #222222;
  color: red;
}

nav li:hover {
  background-color: #141414;
  color: rgb(141, 48, 48);
}

/*** YOUTUBE EMBED ***/
.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

/*** RESPONSIVE IMAGE ***/
.responsive-image {
  max-width: 100%;
  height: auto;
}

.responsive-image img {
  margin-top: 5%;
  width: 100%;
  height: auto;
}
/***** END OF CSS FOR MOBILE *****/

/******** MEDIA QUERIES ***********/
@media only screen and (max-width: 600px) {
  /*body {
    background-color: lightblue;
  }*/
}

[class*="col-"] {
  width: 100%;
}

